}

.Pane{

  position: relative;

  margin-top: -10vh;

/*
  padding: 20px;

*/

  padding: 0px;

  background-color: white;

  color: black;

  border-style: solid;
  border-width: 2px;
  border-radius: 32px;
  border-color: #4ce300;

  z-index: 999;

  max-width: 90vw;

  height: -webkit-max-content;

  height: max-content;

  word-wrap: break-word;
}



.gradient{

  padding: 5vh 10vw 5vh 10vw;

  min-height: 100vh;
  display: -webkit-flex;
  display: flex;

  -webkit-justify-content: center;

          justify-content: center;

  background-image: linear-gradient(to bottom, #fc5602, transparent 125%);
}



#Tio-Misa--banner {



  width: 100vw;

}


.Home--Display{
  display: -webkit-flex;
  display: flex;

  -webkit-justify-content: space-between;

          justify-content: space-between;

  width: 100%;

  word-wrap: break-word;
  overflow-wrap: break-word;
  white-space: normal;

}

.Home--Section{

  position: relative;

  float: left;

  table-layout: fixed;


 line-height: normal;


}



.Home-page--title{

  margin-top: 100px;

}

.Home-page--title h1 {

  font-family: 'Berkshire Swash', cursive;
  font-size: 50px;
  text-align: center;

  color: #08da94;
  text-shadow: 1px 1px black;

}

.Home-page--title h2 {

  font-family: 'Berkshire Swash', cursive;
  font-size: 40px;

  margin-left: 30px;

  color: #08da94;
  text-shadow: 1px 1px black;

}




.Header{

  border-radius: 30px 30px 0px 0px;

  min-height: 8vh;
  min-width: 90vw;

  display: -webkit-flex;

  display: flex;


  color: white;

  background-color: #4ce300;
  
}

.text-area{

  position: relative;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;

  margin: 0px 80pt 0px 50pt;

  width: 100%;
}

.ScreenName{

  display: inline-block;

  font-size: 20pt;
  font-weight: bold;


}

.ScreenMap{

  display: inline-block;

  font-size: 16pt;
  font-weight: bold;

}

.Header--perfil-image  .PerfilImage{

  margin-left: -10px;

  border-radius: 100%;
  border-width: 0;
  border-color: #4ce300;

  position: relative;

  width: 130%;
  height: 130%;
}

.Header--perfil-image .PerfilImage--Background{

  position: absolute;

  border-style: solid;
  border-color:#4ce300;
  border-width: 5px;

  -webkit-align-items: flex-end;

          align-items: flex-end;

  width: 65pt;
  height: 65pt;

  margin-top: -30pt;
  margin-left: -55pt;

}

.Header--logo {

  position: absolute;

  width: 60pt;
  height: 60pt;
}

.Header--logo .PerfilImage--Background{

  position: absolute;

  width: 60pt;
  height: 60pt;

  margin-top: -25pt;
  margin-left: -15pt;

}



.PerfilImage--Background{

  background-color: #fe5d00;

  border-radius: 100%;


  min-width: 55pt;
  min-height: 55pt;


  display:-webkit-flex;


  display:flex;
  -webkit-align-items: center;
          align-items: center;


}

.PerfilImage{

  border-style: solid;
  border-radius: 100%;
  border-width: 5px;
  border-color: #4ce300;

  position: relative;

  width: 100%;
  height: 100%;
}


.Menu{

  position: relative;

  float: right;

  padding-left: 10pt;
  padding-right: 15pt;


  /*95.3%*/

  height: auto;

  min-width: -webkit-max-content;

  min-width: max-content;

  background-color: #4ce300;

  border-radius: 0 0 30px 0;

  color: white;
  font-size: 15pt;
  font-weight: bolder;
}

.Menu h2 {

  padding-bottom: 20px;
  padding-top: 20px
}

.Menu .pages{
  padding-left: 10px;
}



.nav-button{

  background-color: transparent;

  margin-bottom: 50px;
  line-height: 0px;
}

.nav-link{
  padding-bottom: 0px;
  margin: 0px;
  line-height: 0px;
}

.nav-button a {

  border-style: solid;
  border-width: 0px;
  border-color: white;


  display: block;

  text-decoration: none;
  color: white;

  transition: all ease-in 0.1s;
}

.nav-button--border-bar {

  border-width: 0px;

  width: 0px;
  height: 0px;

  background-color: white;


  transition: all ease-in-out 0.3s;
}

nav .active {

  padding-top: 20px;
  padding-left: 5px ;
  padding-bottom: 20px;

  border-style: solid;
  border-color: white;
  border-left-width: 6px;

}


.nav-button:hover .nav-button--border-bar{

  width: 90%;
  height: 2pt;
}



